header {
    margin: 20px 10px;
}

header nav {
    background-color: beige;
    padding: 5px 5px;
}

header nav ul.nav-items {
  list-style: none;
  padding: 0;
  display: flex;
  justify-content: space-between;
  align-items: end;
}  

.nav-logo {
    order: -1
}

header nav ul li {
    display: inline;
    margin-left: 1em;
    margin-right: 1em;
}


header nav ul li a {
    text-decoration: none;
}